import { BASE_IMAGE_URL,bindUser,getUserInfo } from "@/utils/api";

export default {
	data() {
		return {
			bind_bg: BASE_IMAGE_URL+"bind_bg.png",
			name_bg: BASE_IMAGE_URL+"name_bg.png",
			mobile_bg: BASE_IMAGE_URL+"mobile_bg.png",
			verify_bg: BASE_IMAGE_URL+"verify_bg.png",
			email_bg: BASE_IMAGE_URL+"email_bg.png",
			card_bg: BASE_IMAGE_URL+"card_bg.png",
			info_bg: BASE_IMAGE_URL+"info_bg.png",
			register_bg: BASE_IMAGE_URL+"register_bg.png",
			code_bg: BASE_IMAGE_URL+"code_bg.png",
			m_truename: '',
			m_email: '',
			m_id_number: '',
			m_phone: '',
			phone_code: '',
			codeBtn: {
				text: 'SMS CODE',
				waitingCode: false,
				count: this.seconds
			},
			seconds: 60,
			m_code: ''
		}
	},
	methods: {
		async bindUser(){
			if(!this.m_truename){
				uni.showToast({
					icon: 'none',
					title: '请输入姓名',
					duration: 1000,
					position:'bottom'
				});
				return;
			}
			
			if(!this.m_code){
				uni.showToast({
					icon: 'none',
					title: '请输入CODE',
					duration: 1000,
					position:'bottom'
				});
				return;
			}
			
			
			let params = {
				m_openid: uni.getStorageSync("openid"),
				m_truename: this.m_truename,
				m_code: this.m_code,
				type: 2
			};
			let res = await bindUser(params);
			if(res.code == 1000){
				uni.showToast({
					icon: 'none',
					title: '注册成功',
					duration: 1000,
					position:'bottom'
				});
				
				setTimeout(() => {
					this.getUserInfo();
				}, 1000);
			}else{
				uni.showToast({
					icon: 'none',
					title: res.msg,
					duration: 1000
				});
			}
		},
		async getUserInfo(){
			let res = await getUserInfo(uni.getStorageSync('openid'));
			console.log(res);
			if(res.code == 1000){
				// wx.setStorageSync('userInfo',res.data);
				uni.redirectTo({
					url:'/pages/main/main'
				})
			}else{
				uni.showToast({
					icon: 'none',
					title: res.msg,
					duration: 1000
				});
			}
		}
	}
}
